@import "utils/mixins";

.grid {

  @include onTablet {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    column-gap: 30px;
    grid-template-rows: repeat(7, 156px);
    row-gap: 24px;
  }

  @include onSmallDesktop {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    column-gap: 20px;
    grid-template-rows: repeat(5, 156px);
  }

  @include onDesktop {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    column-gap: 30px;
  }

  &__item {
    @for $start from 1 through 6 {
      @for $end from $start through 6 {
        &--tablet-#{$start}-#{$end} {
          @include onTablet {
            grid-column: #{$start} / #{$end + 1};
          }
        }
      }
    }

    &--tablet-1-3 {
      @include onTablet {
        grid-column: 1 / span 3;
      }

      &--001 {
        grid-row: 3 / span 2;
      }

      &--003 {
        grid-row: 5 / span 2;
      }
    }

    &--tablet-4-6 {
      @include onTablet {
        grid-column: 4 / span 3;
      }

      &--002 {
        grid-row: 2 / span 2;
      }

      &--004 {
        grid-row: 4 / span 2;
      }
    }

    @for $start from 1 through 12 {
      @for $end from $start through 12 {
        &--small-desktop-#{$start}-#{$end} {
          @include onSmallDesktop {
            grid-column: #{$start} / #{$end + 1};
          }
        }
      }

      &--small-desktop-7-9 {
        @include onSmallDesktop {
          grid-column: 7 / span 3;
        }

        &--001 {
          grid-row: 2 / span 2;
        }

        &--003 {
          grid-row: 4 / span 2;
        }
      }

      &--small-desktop-10-12 {
        @include onSmallDesktop {
          grid-column: 10 / span 3;
        }

        &--002 {
          grid-row: 1 / span 2;
        }

        &--004 {
          grid-row: 3 / span 2;
        }
      }
    }
  }

  &__service {
    @include onTablet {
      grid-template-rows: repeat(7, 156px);
      row-gap: 24px;
    }

    @include onSmallDesktop {
      grid-template-rows: repeat(5, 156px);
    }
  }

  &__testimonials {
    @include onTablet {
      grid-template-rows: none;
    }
  }

  &__contact {
    @include onTablet {
      grid-template-rows: none;
    }
  }
}
